import React, { useState, useEffect } from 'react'
import { View, Text, Image, Button, Input } from '@tarojs/components'
import { AtIcon, AtModal, AtModalHeader, AtModalContent, AtModalAction } from 'taro-ui'
import Taro, { useDidShow } from '@tarojs/taro'
import { walletService } from '@/services/wallet.service'
import { getLocationService } from '@/services/location'
import {
  getCurrentUserInfo,
  maskPhone,
  checkAndAutoLogin,
  wechatLogin,
  bindPhone,
  WechatLoginResult,
  UserInfo
} from '@/utils/user'
import PhoneAuth from '@/components/PhoneAuth'
import NewUserVoucherModal from '@/components/NewUserVoucherModal'
import LogoImg from '@/assets/icons/logo.png'
import { voucherService } from '@/services/voucher.service'
import './index.scss'

const Mine: React.FC = () => {
  const [balance, setBalance] = useState(0)
  const [userInfo, setUserInfo] = useState<UserInfo | null>(null)
  const [loading, setLoading] = useState(false)
  const [locationEnabled, setLocationEnabled] = useState(false)

  // Êñ∞‰∫∫Á§ºÂà∏ÂºπÁ™ó
  const [showNewUserVoucher, setShowNewUserVoucher] = useState(false)
  const [voucherInfo, setVoucherInfo] = useState<{ discountPercentage: number }>({
    discountPercentage: 0
  })

  // ÊâãÊú∫Âè∑ÁªëÂÆöÂºπÁ™óÁä∂ÊÄÅ
  const [showBindPhoneModal, setShowBindPhoneModal] = useState(false)
  const [phoneInput, setPhoneInput] = useState('')
  const [bindingPhone, setBingingPhone] = useState(false)
  const [currentOpenid, setCurrentOpenid] = useState('')

  useEffect(() => {
    initUser()
    checkLocation()
  }, [])

  // È°µÈù¢ÊòæÁ§∫Êó∂Âà∑Êñ∞Êï∞ÊçÆ
  useDidShow(() => {
    refreshUserData()
    checkLocation()
  })

  // Ê£ÄÊü•ÂÆö‰ΩçÁä∂ÊÄÅ
  const checkLocation = async () => {
    try {
      await getLocationService.getCurrentLocation()
      setLocationEnabled(true)
    } catch (error) {
      setLocationEnabled(false)
    }
  }

  // ÂàùÂßãÂåñÁî®Êà∑‰ø°ÊÅØ
  const initUser = async () => {
    setLoading(true)
    try {
      // ÂÖàÂ∞ùËØï‰ªéÊú¨Âú∞Ëé∑ÂèñÁî®Êà∑‰ø°ÊÅØ
      const localUser = getCurrentUserInfo()
      if (localUser && localUser.phone) {
        setUserInfo(localUser)
        fetchBalance()
        // ÂêéÂè∞ÈùôÈªòÂ∞ùËØïÂà∑Êñ∞Áî®Êà∑‰ø°ÊÅØ
        checkAndAutoLogin().then(freshUser => {
          if (freshUser) {
            setUserInfo(freshUser)
          }
        })
      } else {
        // Ê≤°ÊúâÊú¨Âú∞Áî®Êà∑ÔºåÂ∞ùËØïËá™Âä®ÁôªÂΩï
        const userInfo = await checkAndAutoLogin()
        if (userInfo) {
          setUserInfo(userInfo)
          fetchBalance()
        } else {
          // ÈúÄË¶ÅÊâãÂä®Ëß¶ÂèëÁôªÂΩï
          console.log('ÈúÄË¶ÅÁî®Êà∑ÊâãÂä®ÁôªÂΩï')
          // Á°Æ‰øùuserInfo‰∏∫nullÔºåÊòæÁ§∫ÁôªÂΩïÊåâÈíÆ
          setUserInfo(null)
        }
      }
    } catch (error) {
      console.error('ÂàùÂßãÂåñÁî®Êà∑Â§±Ë¥•:', error)
      // Â§±Ë¥•Êó∂‰πüË¶ÅÁ°Æ‰øùÊòæÁ§∫ÁôªÂΩïÊåâÈíÆ
      setUserInfo(null)
    } finally {
      setLoading(false)
    }
  }

  // Âà∑Êñ∞Áî®Êà∑Êï∞ÊçÆ
  const refreshUserData = async () => {
    if (userInfo?.phone) {
      fetchBalance()
    }
  }

  const fetchBalance = async () => {
    try {
      const currentBalance = await walletService.getBalance()
      setBalance(currentBalance)
    } catch (error) {
      console.error('Ëé∑Âèñ‰ΩôÈ¢ùÂ§±Ë¥•:', error)
    }
  }

  const menuItems = [
    {
      icon: 'file-generic',
      title: 'ÊàëÁöÑËÆ¢Âçï',
      path: '/pages/order/list/index',
      arrow: true
    },
    {
      icon: 'money',
      title: 'ÊàëÁöÑÂà∏ÂåÖ',
      path: '/pages/mine/vouchers/index',
      arrow: true
    },
    // {
    //   icon: 'gift',
    //   title: 'ÈÇÄËØ∑ÊúâÂ•ñ',
    //   path: '/pages/mine/invite/index',
    //   arrow: true
    // },
    // {
    //   icon: 'phone',
    //   title: 'ËÅîÁ≥ªÂÆ¢Êúç',
    //   path: '/pages/mine/contact/index',
    //   arrow: true
    // },
    {
      icon: 'help',
      title: 'ÂÖ≥‰∫éÊàë‰ª¨',
      path: '/pages/mine/about/index',
      arrow: true
    }
  ]

  const handleMenuClick = (item: typeof menuItems[0]) => {
    Taro.navigateTo({ url: item.path })
  }

  // Â§ÑÁêÜÂæÆ‰ø°ÁôªÂΩï
  const handleLogin = async () => {
    if (loading) return

    setLoading(true)
    try {
      const loginResult = await wechatLogin()

      if (loginResult.needBindPhone) {
        // ÈúÄË¶ÅÁªëÂÆöÊâãÊú∫Âè∑ - ËÆæÁΩÆopenid‰æõPhoneAuthÁªÑ‰ª∂‰ΩøÁî®
        setCurrentOpenid(loginResult.openid)
        setShowBindPhoneModal(true)
      } else if (loginResult.userInfo) {
        // ÁôªÂΩïÊàêÂäü
        setUserInfo(loginResult.userInfo)
        fetchBalance()

        // Ê£ÄÊü•ÊòØÂê¶‰∏∫Êñ∞Áî®Êà∑Âπ∂ÊòæÁ§∫Á§ºÂà∏ÂºπÁ™ó
        checkAndShowNewUserVoucher()

        Taro.showToast({
          title: 'ÁôªÂΩïÊàêÂäü',
          icon: 'success'
        })
      }
    } catch (error) {
      console.error('ÂæÆ‰ø°ÁôªÂΩïÂ§±Ë¥•:', error)
      Taro.showToast({
        title: 'ÁôªÂΩïÂ§±Ë¥•ÔºåËØ∑ÈáçËØï',
        icon: 'error'
      })
    } finally {
      setLoading(false)
    }
  }

  // Â§ÑÁêÜÊâãÊú∫Âè∑ÁªëÂÆö
  const handleBindPhone = async () => {
    if (!phoneInput.trim()) {
      Taro.showToast({
        title: 'ËØ∑ËæìÂÖ•ÊâãÊú∫Âè∑',
        icon: 'error'
      })
      return
    }

    // ÁÆÄÂçïÁöÑÊâãÊú∫Âè∑È™åËØÅ
    const phoneRegex = /^1[3-9]\d{9}$/
    if (!phoneRegex.test(phoneInput.trim())) {
      Taro.showToast({
        title: 'ÊâãÊú∫Âè∑Ê†ºÂºè‰∏çÊ≠£Á°Æ',
        icon: 'error'
      })
      return
    }

    setBingingPhone(true)
    try {
      await bindPhone(currentOpenid, phoneInput.trim())

      // ÁªëÂÆöÊàêÂäüÔºåËé∑ÂèñÊúÄÊñ∞Áî®Êà∑‰ø°ÊÅØ
      const latestUserInfo = getCurrentUserInfo()
      if (latestUserInfo) {
        setUserInfo(latestUserInfo)
        fetchBalance()
      }

      setShowBindPhoneModal(false)
      setPhoneInput('')

      Taro.showToast({
        title: 'ÁªëÂÆöÊàêÂäü',
        icon: 'success'
      })
    } catch (error: any) {
      console.error('ÊâãÊú∫Âè∑ÁªëÂÆöÂ§±Ë¥•:', error)

      // Ê†πÊçÆÈîôËØØÁ±ªÂûãÊòæÁ§∫‰∏çÂêåÁöÑÊèêÁ§∫
      let errorMessage = 'ÁªëÂÆöÂ§±Ë¥•ÔºåËØ∑ÈáçËØï'

      // Ê£ÄÊü•ÊòØÂê¶ÊòØÊâãÊú∫Âè∑Â∑≤Ë¢´Âç†Áî®ÁöÑÈîôËØØ
      if (error?.message?.includes('Â∑≤') || error?.response?.data?.error?.message?.includes('Â∑≤')) {
        errorMessage = 'ËØ•ÊâãÊú∫Âè∑Â∑≤Ë¢´‰ΩøÁî®'
      } else if (error?.response?.status === 500 || error?.message?.includes('500')) {
        errorMessage = 'ËØ•ÊâãÊú∫Âè∑Â∑≤Ë¢´Âç†Áî®'
      } else if (error?.message?.includes('ÁΩëÁªú')) {
        errorMessage = 'ÁΩëÁªúÈîôËØØÔºåËØ∑Á®çÂêéÈáçËØï'
      }

      Taro.showToast({
        title: errorMessage,
        icon: 'none',
        duration: 2000
      })
    } finally {
      setBingingPhone(false)
    }
  }

  // ÂèñÊ∂àÊâãÊú∫Âè∑ÁªëÂÆö
  const handleCancelBindPhone = () => {
    setShowBindPhoneModal(false)
    setPhoneInput('')
    setCurrentOpenid('')
  }

  const handleBalanceClick = () => {
    Taro.navigateTo({
      url: '/pages/mine/balance/index'
    })
  }

  // Ê£ÄÊü•Âπ∂ÊòæÁ§∫Êñ∞Áî®Êà∑Á§ºÂà∏ÂºπÁ™ó
  const checkAndShowNewUserVoucher = () => {
    const voucherInfo = voucherService.getNewUserVoucherInfo()
    if (voucherInfo.hasVoucher && voucherInfo.discountPercentage) {
      // Ê£ÄÊü•ÊòØÂê¶Â∑≤ÁªèÊòæÁ§∫ËøáÔºàÈÅøÂÖçÈáçÂ§çÊòæÁ§∫Ôºâ
      const hasShown = Taro.getStorageSync('newUserVoucherShown')
      if (!hasShown) {
        setVoucherInfo({ discountPercentage: voucherInfo.discountPercentage })
        setTimeout(() => {
          setShowNewUserVoucher(true)
        }, 1500) // Âª∂ËøüÊòæÁ§∫ÔºåËÆ©Áî®Êà∑ÂÖàÁúãÂà∞ÁôªÂΩïÊàêÂäü
      }
    }
  }

  // Â§ÑÁêÜÂÖ≥Èó≠Êñ∞‰∫∫Á§ºÂà∏ÂºπÁ™ó
  const handleCloseNewUserVoucher = () => {
    setShowNewUserVoucher(false)
    // Ê†áËÆ∞Â∑≤ÊòæÁ§∫
    Taro.setStorageSync('newUserVoucherShown', true)
  }

  // Â§ÑÁêÜ‰ΩøÁî®Êñ∞‰∫∫Á§ºÂà∏
  const handleUseNewUserVoucher = () => {
    setShowNewUserVoucher(false)
    Taro.setStorageSync('newUserVoucherShown', true)
  }

  return (
    <View className="mine-page">
      <View className="header-section">
        <View className="user-card">
          <View className="user-info">
            <Image
              className="avatar"
              src={userInfo?.avatar || LogoImg}
              mode="aspectFill"
            />
            {userInfo ? (
              <Text className="phone">
                {userInfo.nickname || userInfo.username || maskPhone(userInfo.phone)}
              </Text>
            ) : (
              <Button
                className="login-btn"
                onClick={handleLogin}
                loading={loading}
                disabled={loading}
                size="mini"
                type="primary"
              >
                {loading ? 'ÁôªÂΩï‰∏≠...' : 'ÂæÆ‰ø°ÁôªÂΩï'}
              </Button>
            )}
          </View>
          {userInfo && (
            <>
              {/* ÊâãÊú∫Âè∑‰ø°ÊÅØÂíåÊç¢ÁªëÂäüËÉΩ */}
              <View className="phone-info">
                <Text className="phone-label">ÊâãÊú∫Âè∑: </Text>
                <Text className="phone-number">{maskPhone(userInfo.phone)}</Text>
                <PhoneAuth
                  type="change"
                  buttonText="Êç¢Áªë"
                  buttonSize="mini"
                  buttonType="default"
                  onSuccess={(phone) => {
                    console.log('Êç¢ÁªëÊàêÂäüÔºåÊñ∞ÊâãÊú∫Âè∑:', phone)
                    Taro.showToast({
                      title: 'Êç¢ÁªëÊàêÂäü',
                      icon: 'success'
                    })
                    // Âà∑Êñ∞Áî®Êà∑‰ø°ÊÅØ
                    initUser()
                  }}
                />
              </View>
              {/* ‰ΩôÈ¢ùÂíåÂÆö‰Ωç‰ø°ÊÅØ */}
              <View className="balance-location-row">
                <View className="balance-info" onClick={handleBalanceClick}>
                  <Text className="balance-label">‰ΩôÈ¢ù: </Text>
                  <Text className="balance-amount">¬• {balance.toFixed(2)}</Text>
                  <AtIcon value="chevron-right" size="16" color="#fff" />
                </View>
                <View className="location-info">
                  <Text className="location-text">
                    {locationEnabled ? 'ÂÆö‰ΩçÂ∑≤ÂêØÁî®' : 'ÂÆö‰ΩçÊú™ÂêØÁî®'}
                  </Text>
                </View>
              </View>
            </>
          )}
        </View>
      </View>

      <View className="menu-section">
        {menuItems.map((item, index) => (
          <View 
            key={index}
            className="menu-item" 
            onClick={() => handleMenuClick(item)}
          >
            <View className="menu-left">
              <AtIcon 
                prefixClass="icon" 
                value={item.icon} 
                size="20" 
                color="#666"
              />
              <Text className="menu-title">{item.title}</Text>
            </View>
            <View className="menu-right">
              {item.badge && (
                <View className="badge">{item.badge}</View>
              )}
              {item.arrow && (
                <AtIcon value="chevron-right" size="16" color="#999" />
              )}
            </View>
          </View>
        ))}
      </View>

      {/* ÊâãÊú∫Âè∑ÁªëÂÆöÂºπÁ™ó - ‰ΩøÁî®ÂæÆ‰ø°ÊâãÊú∫Âè∑ÁªÑ‰ª∂ */}
      <AtModal
        isOpened={showBindPhoneModal}
        onCancel={handleCancelBindPhone}
      >
        <AtModalHeader>ÁªëÂÆöÊâãÊú∫Âè∑</AtModalHeader>
        <AtModalContent>
          <View className="bind-phone-content">
            <Text className="bind-phone-tips">
              ‰ΩøÁî®ÂæÆ‰ø°ÁªëÂÆöÁöÑÊâãÊú∫Âè∑Âø´ÈÄüÁôªÂΩï
            </Text>
            <View className="phone-auth-wrapper">
              <PhoneAuth
                type="bind"
                openid={currentOpenid}
                onSuccess={(phone) => {
                  console.log('ÁªëÂÆöÊàêÂäüÔºåÊâãÊú∫Âè∑:', phone)
                  setShowBindPhoneModal(false)
                  setPhoneInput('')
                  // Âà∑Êñ∞Áî®Êà∑‰ø°ÊÅØ
                  const latestUserInfo = getCurrentUserInfo()
                  if (latestUserInfo) {
                    setUserInfo(latestUserInfo)
                    fetchBalance()
                  }
                }}
                onCancel={() => {
                  console.log('Áî®Êà∑ÂèñÊ∂àÁªëÂÆö')
                }}
              />
            </View>
            <Text className="bind-phone-privacy">
              ÊéàÊùÉÂêéÂ∞ÜËé∑ÂèñÊÇ®ÁöÑÂæÆ‰ø°ÊâãÊú∫Âè∑Áî®‰∫éË¥¶Âè∑ÁôªÂΩï
            </Text>
          </View>
        </AtModalContent>
        <AtModalAction>
          <Button onClick={handleCancelBindPhone}>Á®çÂêéÁªëÂÆö</Button>
        </AtModalAction>
      </AtModal>

      {/* Êñ∞‰∫∫Á§ºÂà∏ÂºπÁ™ó */}
      <NewUserVoucherModal
        isOpened={showNewUserVoucher}
        discountPercentage={voucherInfo.discountPercentage}
        onClose={handleCloseNewUserVoucher}
        onUse={handleUseNewUserVoucher}
      />
    </View>
  )
}

export default Mine