import React from 'react'
import { View, Button } from '@tarojs/components'
import Taro from '@tarojs/taro'
import { post } from '@/utils/request'
import { setUserInfo, fetchUserInfo, getCurrentUserInfo } from '@/utils/user'
import './index.scss'

interface PhoneAuthProps {
  type: 'bind' | 'change'  // ÁªëÂÆöÊàñÊç¢Áªë
  openid?: string  // ÁªëÂÆöÊó∂ÈúÄË¶Å‰º†ÂÖ•openid
  onSuccess?: (phone: string) => void
  onCancel?: () => void
  buttonText?: string
  buttonSize?: 'default' | 'mini'
  buttonType?: 'default' | 'primary' | 'warn'
}

/**
 * ÂæÆ‰ø°ÊâãÊú∫Âè∑ÊéàÊùÉÁªÑ‰ª∂
 * ‰ΩøÁî®ÂæÆ‰ø°ÂéüÁîüËÉΩÂäõËé∑ÂèñÊâãÊú∫Âè∑ÔºåÊó†ÈúÄÁü≠‰ø°È™åËØÅÁ†Å
 */
const PhoneAuth: React.FC<PhoneAuthProps> = ({
  type,
  openid,
  onSuccess,
  onCancel,
  buttonText,
  buttonSize = 'default',
  buttonType = 'primary'
}) => {

  const handleGetPhoneNumber = async (e: any) => {
    console.log('ÊâãÊú∫Âè∑ÊéàÊùÉÂõûË∞É:', e.detail)
    console.log('Ëé∑ÂèñÂà∞code:', e.detail.code)
    console.log('ÂΩìÂâçÊó∂Èó¥:', new Date().toISOString())
    console.log('ÂΩìÂâçopenid:', openid)

    if (e.detail.errMsg === 'getPhoneNumber:ok') {
      // Áî®Êà∑ÂêåÊÑèÊéàÊùÉ
      const { code } = e.detail

      if (!code) {
        Taro.showToast({
          title: 'Ëé∑ÂèñÊâãÊú∫Âè∑Â§±Ë¥•',
          icon: 'none'
        })
        return
      }

      console.log('ÂáÜÂ§áÂèëÈÄÅËØ∑Ê±ÇÔºåcode:', code)
      console.log('ËØ∑Ê±ÇÊó∂Èó¥:', new Date().toISOString())

      try {
        Taro.showLoading({ title: 'Â§ÑÁêÜ‰∏≠...' })

        let result
        if (type === 'bind') {
          // È¶ñÊ¨°ÁªëÂÆöÊâãÊú∫Âè∑
          if (!openid) {
            throw new Error('Áº∫Â∞ëopenidÂèÇÊï∞')
          }

          console.log('ÂèëÈÄÅÁªëÂÆöËØ∑Ê±ÇÔºåÂèÇÊï∞:', { openid, code })
          const response = await post('/users/bind-phone-wx', {
            openid,
            code
          })
          console.log('ÁªëÂÆöÂìçÂ∫î:', response)

          result = response.data

          // Ëé∑ÂèñÂÆåÊï¥Áî®Êà∑‰ø°ÊÅØÂπ∂‰øùÂ≠ò
          if (result.phone) {
            const userInfo = await fetchUserInfo(result.phone)
            if (userInfo) {
              setUserInfo(userInfo)
            }
          }

        } else {
          // Êç¢ÁªëÊâãÊú∫Âè∑
          const currentUser = getCurrentUserInfo()
          if (!currentUser) {
            throw new Error('ËØ∑ÂÖàÁôªÂΩï')
          }

          const response = await post('/users/change-phone-wx', {
            userId: currentUser.id,
            oldPhone: currentUser.phone,
            code
          })

          result = response.data

          // Êõ¥Êñ∞Êú¨Âú∞Áî®Êà∑‰ø°ÊÅØ
          if (result.phone) {
            const userInfo = await fetchUserInfo(result.phone)
            if (userInfo) {
              setUserInfo(userInfo)
            }
          }
        }

        Taro.hideLoading()

        if (result && result.phone) {
          Taro.showToast({
            title: type === 'bind' ? 'ÁªëÂÆöÊàêÂäü' : 'Êç¢ÁªëÊàêÂäü',
            icon: 'success'
          })

          // ÂõûË∞ÉÊàêÂäüÂáΩÊï∞
          onSuccess?.(result.phone)
        } else {
          throw new Error('Êìç‰ΩúÂ§±Ë¥•')
        }

      } catch (error: any) {
        Taro.hideLoading()
        console.error('ÊâãÊú∫Âè∑Êìç‰ΩúÂ§±Ë¥•:', error)
        console.error('ÈîôËØØËØ¶ÊÉÖ:', {
          message: error.message,
          response: error.response,
          status: error.response?.status,
          data: error.response?.data
        })

        // ÊòæÁ§∫ÂÖ∑‰ΩìÈîôËØØ‰ø°ÊÅØ
        const errorMsg = error.message || (type === 'bind' ? 'ÁªëÂÆöÂ§±Ë¥•' : 'Êç¢ÁªëÂ§±Ë¥•')
        Taro.showToast({
          title: errorMsg,
          icon: 'none',
          duration: 2000
        })
      }

    } else if (e.detail.errMsg === 'getPhoneNumber:fail user deny') {
      // Áî®Êà∑ÊãíÁªùÊéàÊùÉ
      console.log('Áî®Êà∑ÊãíÁªùÊéàÊùÉÊâãÊú∫Âè∑')

      if (type === 'bind') {
        Taro.showModal({
          title: 'Ê∏©È¶®ÊèêÁ§∫',
          content: 'ÈúÄË¶ÅÊÇ®ÁöÑÊâãÊú∫Âè∑ÊâçËÉΩ‰∏∫ÊÇ®Êèê‰æõÈ¢ÑÁ∫¶ÊúçÂä°',
          showCancel: true,
          confirmText: 'Áü•ÈÅì‰∫Ü',
          success: (res) => {
            if (res.cancel) {
              onCancel?.()
            }
          }
        })
      } else {
        Taro.showToast({
          title: 'Â∑≤ÂèñÊ∂àÊç¢Áªë',
          icon: 'none'
        })
        onCancel?.()
      }

    } else {
      // ÂÖ∂‰ªñÈîôËØØ
      console.error('Ëé∑ÂèñÊâãÊú∫Âè∑Â§±Ë¥•:', e.detail.errMsg)
      Taro.showToast({
        title: 'Ëé∑ÂèñÊâãÊú∫Âè∑Â§±Ë¥•',
        icon: 'none'
      })
    }
  }

  // Ê†πÊçÆtypeÂíåbuttonSizeÂÜ≥ÂÆöÊåâÈíÆÊñáÊú¨
  const getButtonText = () => {
    if (buttonText) return buttonText

    if (type === 'bind') {
      return buttonSize === 'mini' ? 'ÁªëÂÆö' : '‰∏ÄÈîÆÁªëÂÆöÊâãÊú∫Âè∑'
    } else {
      return buttonSize === 'mini' ? 'Êõ¥Êç¢' : 'Êõ¥Êç¢ÊâãÊú∫Âè∑'
    }
  }

  return (
    <View className="phone-auth-component">
      <Button
        className={`phone-auth-btn phone-auth-btn--${buttonSize}`}
        type={buttonType}
        size={buttonSize}
        openType="getPhoneNumber"
        onGetPhoneNumber={handleGetPhoneNumber}
      >
        {getButtonText()}
      </Button>
    </View>
  )
}

export default PhoneAuth